@use 'variables' as *;
@use 'mixins' as *;

body {
    color: var(--text-color-1);
    transition: background-color 0.2s ease-out, color 0.2s ease-in;
    background-image: url('../assets/images/pattern-background-desktop-light.svg'), linear-gradient(var(--background-color-1), var(--background-color-1));
    background-repeat: no-repeat;
    background-size: cover;

    @include respond-to('medium') {
        background-image: url('../assets/images/pattern-background-tablet-light.svg'), linear-gradient(var(--background-color-1), var(--background-color-1));
    }

    @include respond-to('small') {
        background-image: url('../assets/images/pattern-background-mobile-light.svg'), linear-gradient(var(--background-color-1), var(--background-color-1));
    }
}

main {
    padding: 8.3rem 2.4rem;
    overflow-x: hidden;
    position: relative;

    @include respond-to('medium') {
        padding: 4rem 2.4rem;
    }
}

h1,
h2,
h3,
h4,
h5,
h6 {
    transition: color 1s ease-in;
    color: var(--heading-color-1);
}

body.dark-mode {
    --background-color-1: #{$color-neutral-900};
    --background-color-2: #{$color-neutral-800};
    --text-color-1: #{$color-neutral-400};
    --heading-color-1: #{$color-neutral-100};
    --shadow-color-1: #{$color-neutral-900};

    background-image: url('../assets/images/pattern-background-desktop-dark.svg'), linear-gradient(var(--background-color-1), var(--background-color-1));

    @include respond-to('medium') {
        background-image: url('../assets/images/pattern-background-tablet-dark.svg'), linear-gradient(var(--background-color-1), var(--background-color-1));
    }

    @include respond-to('small') {
        background-image: url('../assets/images/pattern-background-mobile-dark.svg'), linear-gradient(var(--background-color-1), var(--background-color-1));
    }
}

:focus-visible {
    outline-color: $color-primary;
    outline-offset: 3px;
}

::selection {
    background-color: $color-primary;
    color: $color-neutral-100;
}
